<template>
    <div id="single-blog">
      <h1>{{blog.title}}</h1>
      <article>{{blog.content}}</article>
      <p>
        作者:{{blog.author}}
      </p>
      <p>
        分类:
      </p>
      <ul>
        <li v-for="item  of blog.categories">
          {{item}}
        </li>
      </ul>
    </div>

</template>

<script>
export default {
  name: "SingleBlog",
  data () {
    return {
      id:this.$route.params.id,
      blog: {}
    }
  },
  created() {
    this.$http.get("https://vuedemo-79fd7.firebaseio.com/post/" + this.id +".json")
      .then((data)=> {
        return data.json();
      }).then(function (data) {
        this.blog = data;

    })
  }
}
</script>

<style scoped>
#single-blog{
  background-color: #cccccc;
  width: 800px;
  margin: 100px auto;
  padding: 10px;
}
  ul li{
    display: inline;
  }
  h1{
    text-align: center;
    line-height: 20px;
  }
article{
  border: solid 1px #888888;
}
</style>



