<template>
    <div id="app">
      <span id="span" v-if="display">请求失败</span>
      <h2>添加博客</h2>
      <hr>
      <form v-if="!summited">
        <label>博客标题</label>
        <input type="text" v-model="blog.title" required>
        <label>博客内容</label>
        <textarea v-model="blog.content"></textarea>
        <div id="checkboxes">
          <label>Vue.js</label>
          <input type="checkbox" value="Vue.js" v-model="blog.categories">
          <label>Node.js</label>
          <input type="checkbox" value="Node.js" v-model="blog.categories">
          <label>react.js</label>
          <input type="checkbox" value="react.js" v-model="blog.categories">
          <label>angular.js</label>
          <input type="checkbox" value="angular.js" v-model="blog.categories">
          <label>css</label>
          <input type="checkbox" value="css" v-model="blog.categories">
          <label>other</label>
          <input type="checkbox" value="other" v-model="blog.categories">
        </div>
        <label>作者:</label>
        <select v-model="blog.author">
          <option v-for="author of authors">
            {{author}}
          </option>
        </select>
        <button v-on:click.prevent="post">添加博客</button>
      </form>

        <div id="message" v-if="summited">
          <h3>您的博客已发布</h3>
        </div>
        <div id="preview">
          <h3>博客总览</h3>
          <p>博客标题:{{blog.title}}</p>
          <p>博客内容</p>
          <p>{{blog.content}}</p>
          <p>博客分类:</p>
          <ul>
            <li v-for="category in blog.categories">
              {{category}}
            </li>
          </ul>
          <P>
            作者:{{blog.author}}
          </P>
        </div>

    </div>
</template>

<script>
export default {
  name: "AddBlog",
  data () {
    return{
      blog:{
        title:'',
        content:'',
        categories:[],
        author:""
      },
      authors:["linjunjian","Linxiaoken"],
      summited:false,
      display:false
    }
  },
  methods:{
    post:function () {
      this.$http.post("https://vuedemo-79fd7.firebaseio.com/post.json",this.blog)
        .then(function (data) {
          console.log(data)
          this.summited=true;
        })
        .catch(function () {
          console.log("请求失败")
        this.display = true;
        setTimeout( () => {
          this.display =false
        },2000)
        })
    }
  }
}
</script>

<style scoped>
  #app{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
  }
  label{
    display: block;
    margin: 20px 0 10px;
  }
  input[type="text"]{
    display: block;
    width: 100%;
    padding: 8px;
  }
  #checkboxes label{
    display: inline-block;
    margin-top: 0;
  }
  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }
  button{
    display: block;
    margin: 20px 0;
    background-color: crimson;
    color: #FFF;
    border: 0;
    border-radius:4px;
    font-size: 18px;
    cursor: pointer;
  }
  #preview{
    border:solid 1px #eeeeee;
    margin: 40px 0;
    padding: 20px;
  }
  #preview h3{
    line-height: 20px;
  }
  textarea{
    width: 100%;
    height: 200px;
  }
  #message{
    color: red;
  }
  span{
    position: fixed;
    text-align: center;
    width: 150px;
    margin: 500px auto;
    font-size: 30px;
    color: red;
    background: #cccccc;
  }
</style>



