<template>
    <div id="show-blogs">
      <h1>博客总览</h1>
      <input type="text" v-model="search" placeholder="搜索">
      <div v-rainbow v-for="blog in filteredBlogs" class="single-blog">
        <router-link :to="'/blog/' + blog.id">
            <h2>{{blog.title | to-uppercase}}</h2>
        </router-link>
        <article>
          {{blog.content}}
        </article>
      </div>
    </div>
</template>

<script>
export default {
  name: "ShowBlogs",
  data(){
    return{
      blogs:[],
      search:''
    }
  },
  created() {
    this.$http.get("https://vuedemo-79fd7.firebaseio.com/post.json")
      .then(function (data) {
      return data.json()
      //  this.blogs = data.body.slice(0,10);
      })
      .then(function (data) {//data是 data.json
        var blogsArray = [];
        for (let key in data){
            data[key].id = key;
            blogsArray.push(data[key]);
            this.blogs = blogsArray;
        }
      })
  },
  computed:{
    filteredBlogs:function () {
      var a = this.blogs.filter((item) => {
        return  item.title.match(this.search);//相当于a的值
        // 一个个对比满足这个条件返回item.title给a

      })
        return a
    }
  }

}
</script>

<style scoped>
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
}
  .single-blog{
    padding:20px;
    margin:20px 0;
    box-sizing:border-box;
    background-color: #eee;
  }
</style>



